/* Contenitore principale per il layout */
.container-odds {
  display: flex; /* Utilizza Flexbox per il layout a due colonne */
}

/* Stili per la sezione degli sport disponibili */
.available-sports {
  text-align: center;
  top: 73px;
  position: fixed;
  border-right: 1px solid #ccc;
  flex: 1 1;
  max-height: 100vh;
  overflow-y: auto;
  background: white;
}

/* Stili per la sezione degli upcoming odds e filtri */
.upcoming-odds {
  position: absolute;
  top: 73px;
  left: 26rem;
  width: 70%;
  flex: 2 1;
  overflow-y: auto;
  padding: 20px;
}

/* Puoi anche aggiungere uno stile generale per il contenitore di tutto */
h2 {
  margin-top: 0; /* Rimuove il margine superiore per un aspetto pi√π pulito */
}

.modal-odds {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); /* Sfondo semi-trasparente */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000; /* Assicurati che la modale sia sopra altri contenuti */
  }
  
  .modal-content {
    background: white;
    padding: 20px;
    border-radius: 8px;
    width: 80%;
    max-width: 500px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .modal-content input {
    margin: 5px 0;
  }
  
  .bookmakers-checkboxes {
    display: flex;
    margin-bottom: 20px;
    /* flex-direction: row; */
    /* justify-content: space-evenly; */
    align-items: center;
    flex-wrap: wrap;
}

  .modal-row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  
  .modal-row:last-of-type {
    margin-bottom: 20px;
  }